import React, { Component } from 'react'

import { Input } from 'antd'
const { TextArea } = Input

class QTableItem extends Component {
    constructor(props) {
        super(props)
        this.valueChange = this.valueChange.bind(this)
    }
    valueChange(event) {
        let value = event.target.value
        let index = this.props.index
        this.props.onUpdate(value, index)
    }
    render() {
        let { listInfo } = this.props
        // log('listInfo', listInfo)
        return listInfo.auto ? (
            <TextArea
                value={listInfo.value}
                onChange={this.valueChange}
                placeholder={listInfo.label}
                autoSize={listInfo.auto}
            />
        ) : (
            <Input
                placeholder={listInfo.label}
                value={listInfo.value}
                onChange={this.valueChange}
            />
        )
    }
}

export default QTableItem
